import React, { Component } from 'react';

import Header from '../headerComponent/header';
import Footer from '../footerComponent/footer';

class Contact extends Component {
    render() {
        return (
            <div>
                <Header />
                <div className="contact-page">
                    <div className="contact-title" style={{ backgroundImage: `linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)), url(${require('../../assets/images/contact-image1.jpg')})` }}>
                        <h2>Contact Us</h2>
                    </div>
                </div>
                <div className="contact-content">
                    <h3>Contact WaffleBae</h3>
                    <p>We love hearing from our customers, all questions, comments and feedback are always welcome.</p>
                    <p>If you have a general inquiry or question,  you can call us on <span className="text-warning">095676 89900.</span> Alternatively, you can send us an email on <span className="text-warning">wafflebaetm@gmail.com,</span> and we’ll do our best to reply as quickly as possible.</p>
                    <p>If you are interested in franchise and investment opportunities, please refer to <a className="text-warning" href="#Franchise">this page.</a></p>
                </div>
                <Footer />
            </div>
        );
    }
}

export default Contact;
